<template>
  <v-card 
    elevation="0"
    class="px-1"
  >
    <v-card-title>
      <v-row justify="space-between">
        <v-col cols="12" sm="4">
          Логи
        </v-col>
        <v-col cols="12" sm="4">
          <v-select
            label="Категория" 
            :items="['All', 'Admin', 'Moderator', 'User']"
            v-model="group_type"
          ></v-select>
        </v-col>
        <v-col cols="12" sm="4">
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Поиск"
          ></v-text-field>
        </v-col>
      </v-row>
    </v-card-title>
    <v-data-table
      :headers="headers"
      :items="filteredItems"
      :search="search"
    >
      <template v-slot:item.action>
        <v-btn icon>
          <v-icon small>
            mdi-information-outline 
          </v-icon>
        </v-btn>
      </template> 
    </v-data-table>
  </v-card>
</template>

<script>
export default {
  data: () => ({
    group_type: null,
    search: '',
    headers: [
      { text: 'ID пользователя', align: 'left', value: 'userId' },
      { text: 'Имя пользователя', value: 'userName' },
      { text: 'Роль', value: 'userRole' },
      { text: 'Дата', value: 'date' },
      { text: 'Способ входа', value: 'loginMethod' },
      { text: 'Действия', value: 'action', sortable: false }
    ],
    items: [
      { 
        userId: 21,
        userName: 'Саша',
        userRole: 'moderator',
        date: new Date().toISOString().substr(0, 10),
        loginMethod: 'vkontakte'
      },
      { 
        userId: 22,
        userName: 'Ваня',
        userRole: 'user',
        date: new Date().toISOString().substr(0, 10),
        loginMethod: 'local'
      },
      { 
        userId: 30,
        userName: 'Катя',
        userRole: 'user',
        date: new Date().toISOString().substr(0, 10),
        loginMethod: 'vkontakte'
      },
      { 
        userId: 4,
        userName: 'Даша',
        userRole: 'user',
        date: new Date().toISOString().substr(0, 10),
        loginMethod: 'local'
      },
      { 
        userId: 51,
        userName: 'Леша',
        userRole: 'user',
        date: new Date().toISOString().substr(0, 10),
        loginMethod: 'vkontakte'
      },
      { 
        userId: 6,
        userName: 'Андрей',
        userRole: 'moderator',
        date: new Date().toISOString().substr(0, 10),
        loginMethod: 'vkontakte'
      },
      { 
        userId: 7,
        userName: 'Алекс',
        userRole: 'user',
        date: new Date().toISOString().substr(0, 10),
        loginMethod: 'vkontakte'
      }        
    ]
  }),
  computed: {
    filteredItems() {
      return this.items.filter((item) => {
        return (this.group_type == 'All') ? item : !this.group_type || (item.userRole.toLowerCase() === this.group_type.toLowerCase());
      });
    }
  }
}
</script>
